<template>
	<view class="" style="position: relative;">
		<popUp ref="forgot"></popUp>
		<view @click="goInputEmail" class=""
		hover-class="hover1" hover-start-time=0 hover-stay-time=150
		style="position: absolute; top: 130rpx; left: 78rpx;">
			<image src="../../static/Index/fanHui.png" mode="widthFix" style="width: 25rpx;"></image>
		</view>
		<image class="bgc-picture2" src="../../static/fristPage/bgcL.png" mode="widthFix"></image>
		<view class="" style="background-color: #2b95d0; position: fixed; bottom: 0; z-index: -2; height: 100vh; width: 750rpx;"></view>		
		<view class="" style="height: 279rpx;"></view>
		<view class="" v-if="subNoOkFlag">
			<view class="bgc_FFF" :style="resendE ?' height: 750rpx;' : 'height: 1020rpx;'" style="margin: 0 78rpx; border-radius: 51rpx; ">
				<view class="" style="height: 88rpx;"></view>
				<view class="f45rpx color-H0f649c fw800 ta-c">Reset Email</view>
				<view class="f33rpx color-H0f649c ta-c" style="margin: 37rpx 20rpx 0; letter-spacing: 1rpx;">
					An email with a link to reset your password has been sent to 
				</view>
				<view class="f33rpx ta-c" style="color: #8f9090;">
					{{email}} 
				</view>
				<view @click="goInputEmail" class="bgc-o-r ta-c FFF fw800 forContinue fboxSha" 
				hover-class="hover_a24" hover-start-time=0 hover-stay-time=150 >
					Continue
				</view>
				<view class="ta-c color-H0f649c f33rpx" style="margin: 0 10rpx 39rpx;">
					Didn’t receive the email? Try checking your junk or spam folders, or request a resend
				</view>
				<view @click="resendEmail" class="bgc-o-r ta-c FFF fw800 forContinue fboxSha"
				hover-class="hover_a24" hover-start-time=0 hover-stay-time=150 >
					Resend Email
				</view>
				<view class="" v-if="!resendE">
					<view class="ta-c color-H0f649c f33rpx"  style="margin: 40rpx 10rpx;">
						Recheck your mailbox, or try resend again after 
						<text class="f42rpx fw800" style="color: #eb5120; ">{{sec}} sec</text>
						. 
					</view>
					<view class="ta-c color-H0f649c f33rpx">
						Still didn’t receive the email? 
					</view>
					<view class="df-jcc-aic">
						<view class="ta-c f33rpx fw800" @click="goHelp"
						style="color: #eb5120; margin-top: 10rpx; border-bottom: 1rpx solid #eb5120; line-height: 33rpx;">
							Contact us for assistance
						</view>
					</view>
				</view>
			</view>
		</view>
		<view class="" v-else>
			<view  class="" style="position: absolute; top: 130rpx; left: 78rpx;"
			hover-class="hover1" hover-start-time=0 hover-stay-time=150 >
				<image @click="backRegister" src="../../static/Index/fanHui.png" mode="widthFix" style="width: 25rpx;"></image>
			</view>
			<view class="">
				<view class="bgc_FFF" style="margin: 0 78rpx; border-radius: 51rpx; height: 900rpx;">
					<view class="" style="height: 70rpx;"></view>
					<view class=" ta-c color-H0f649c fw800" style="font-size: 43rpx;"> Need Assistance?</view>
					<view class="ta-c color-H0f649c f33rpx" style="margin: 50rpx 50rpx 42rpx; line-height: 42rpx;">
						 If you need further assistance, 
						 please connect to our customer 
						 service team through phone or email.
					</view>
					<view class="ta-c color-H0f649c fw800 f40rpx" style="">
						Email:
					</view>
					<view class="ta-c f33rpx" 
					style="color: #eb5120; margin: 0 0 50rpx; line-height: 42rpx; letter-spacing: 2rpx;">{{emailss}}</view>
					<view class="ta-c color-H0f649c fw800 f40rpx" style="margin: ;">
						Call Number:
					</view>
					<view class="ta-c f33rpx" style="color: #eb5120; line-height: 42rpx; margin: 0rpx 0 50rpx;">
						{{phoness}}</view>
					<view class="ta-c color-H0f649c f33rpx" 
					style="margin: 0rpx 34rpx 30rpx; line-height: 42rpx;">
						If you’d like to back out, please click below.
					</view>
					<view class="FFF f33rpx fw800 ta-c bgc-o-r" @click="backRegister"
					hover-class="hover_a24" hover-start-time=0 hover-stay-time=150
					style="margin: 33rpx 43rpx 38rpx 48rpx; height: 80rpx; line-height: 80rpx; border-radius: 40rpx;"
					>Cancel
					</view>
				</view>
			</view> 
		</view>

	</view>
</template>

<script>
	export default{
		data(){
			return{
				subNoOkFlag:true,
				resendE:true,
				sec:30,
				email:'',
				emailss:'',
				phoness:''
			}
		},
		methods:{
			goHelp(){
				this.subNoOkFlag = false
				// uni.redirectTo({
				// 	url:'../subscription/contactUS'
				// })
			},
			backRegister(){
				this.subNoOkFlag = true
			},
			async getDate(){
				let _this = this
				const res = await this.$myRequest({
					url:'/api/sub/conf'
				})
				console.log('游戏');
				console.log(res);
				if(res.data.succeeded){
					_this.emailss = res.data.data.email
					_this.phoness = res.data.data.phone
				}
			},
			async	resendEmail(){
				let email = uni.getStorageSync('Email')
				console.log(email);
				this.resendE = false
				const _this = this
				if(this.sec == 30){

					const fn = setInterval(function(){
						_this.sec--
						if(_this.sec == 0){
							clearInterval(fn)
							this.sec = 30
							return false
						}
					},1000) 
					const res = await this.$myRequest({
						url:'/api/auth/recover' + '?email=' + email,
					}) 
					console.log('发送邮件');
					console.log(res);
				}else if(this.sec < 30 && this.sec > 0){
					_this.$refs.forgot.openPopup('Wait 30 sec before pressing "ReSend Email"') 
					return false
				}else if(this.sec == 0){
					this.sec = 30
					const fn1 = setInterval(function(){
						_this.sec--
						if(_this.sec == 0){
							clearInterval(fn1)
						}
					},1000) 
				}
			},
			goInputEmail(){
				uni.redirectTo({
					url:'./inputEmail'
				})
			}
		},
		mounted() {
			
			this.email = uni.getStorageSync('Email')
			console.log('ss'+this.email);
		},
		created() {
			this.getDate()
		}
	}
</script>

<style>
	.bgc-picture2{
		 z-index:-1; 
		 width:100%; 
		 height:100%; 
		 position: fixed;  
		 top:0; 
	}
	.forContinue{
		line-height: 84rpx; 
		margin: 24rpx 44rpx 38rpx; 
		letter-spacing: 2rpx; 
		border-radius: 42rpx; 
		height: 84rpx;
	}
	.fboxSha{
		box-shadow: 0rpx 0rpx 3rpx #000;
	}
</style>
